.hero-section-wrapper {
    position: relative;
}

.hero-section {
    min-height: 100vh;
    text-align: center;
}

.hero-heading {
    font-size: 97px;
    font-weight: 700;
}

.hero-subheading {
    font-size: 25px;
    font-weight: 700;
    margin: 35px 58px;
    line-height: 38px;
}

@media all and (max-width: 1279px) {
    .hero-heading {
        font-size: 82px;
        line-height: 92px;
    }

    .hero-subheading {
        font-size: 25px;
    }
}

@media all and (max-width: 767px) {
    .hero-heading {
        font-size: 55px;
        line-height: 47px;
        width: 270px;
        margin: 60px 18px;
    }

    .hero-subheading {
        font-size: 16px;
        line-height: 22px;
        margin: 35px 18px;
    }

    .hero-section {
        align-items: flex-start;
        text-align: left;
        min-height: 70vh;
    }
}